.App {
  margin: 50px;
  display: flex;
  flex-direction: row;
}

.sidebar {
  max-width: 200px;
}

.spinner {
  margin: auto;
  animation: 10s linear 0s normal none infinite running spinner;
  -webkit-animation: 5s linear 0s normal none infinite running spinner;
}

@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.filter-section {
  min-width: 200px;
}

.filter-section input[type='search']{
  margin: 10px 0;
  padding: 5px;
  border-radius: 5px;
}

.filter-section fieldset{
  display: flex;
  flex-direction: column;
  border: 1px solid lightblue;
  border-radius: 15px;
  margin-top: 15px;
}

.filter-section label:hover {
  cursor: pointer;  
}
.sorter-section {
  display: flex;
  flex-direction: row;
  min-width: 200px;
  justify-content: space-around;
}

.sort-btn {
  border: 1px solid lightblue;
  background-color: white;
  padding: 7px 15px;
  border-radius:  15px;
  text-transform: uppercase;
}

.active {
  background-color: lightblue 
}

.sorter-section button:hover {
  background-color: lightblue;
  cursor: pointer;  
}

.sorter-section button:focus{
  outline:none;
}

.content {
  text-align: center;
  margin: auto;
}

.character-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.notFound{
  font-size: 28px;
  font-weight: bold;
  padding: 40px 150px;
}

.character-info {
  margin: 5px 10px;
  padding: 0 10px 15px 10px;
  width: 180px;
  height: 300px;
  display: flex;
  flex-direction: column;
  border: 1px solid lightblue;
  border-radius: 15px;
  flex-wrap: wrap;
  font-size: 14px;
}

.character-info .title {
  text-align: center;
}

.character-info:hover{
  box-shadow: 0 0 10px lightblue;
  cursor: pointer;  
}

.avatar {
  max-width: 165px;
  border-radius: 115px;
  margin: auto;
}

.page-list {
  justify-content: center;
}

.page-list button,
.current-page {
  padding: 8px 15px;
  margin: 5px;
}
.page-list button{
  border: 1px solid lightblue;
  background-color: white;
  border-radius:  15px;
}

.page-list button:hover,
.checked {
  background-color: lightblue;
  cursor: pointer;  
}

.page-list button:focus{
  outline:none;
}

